'use client'

import { useState, useEffect, useRef } from 'react'
import { useRouter } from 'next/navigation'
import Link from 'next/link'
import { Search, Menu, X } from 'lucide-react'
import { ThemeToggle } from './theme-toggle'

interface Anime {
  mal_id: number
  title: string
  episodes?: number
  images: {
    jpg: {
      small_image_url: string
    }
  }
}

export function Navbar() {
  const router = useRouter()
  const [isOpen, setIsOpen] = useState(false)
  const [mobileSearchOpen, setMobileSearchOpen] = useState(false)
  const [searchQuery, setSearchQuery] = useState('')
  const [results, setResults] = useState<Anime[]>([])
  const [loading, setLoading] = useState(false)
  const dropdownRef = useRef<HTMLDivElement>(null)

  const handleSearch = async (query: string) => {
    if (!query) {
      setResults([])
      return
    }
    setLoading(true)
    try {
      const res = await fetch(`/api/search?q=${encodeURIComponent(query)}`)
      const data = await res.json()
      const uniqueResults = data.data.filter(
        (anime: Anime, i: number, arr: Anime[]) =>
          arr.findIndex(a => a.mal_id === anime.mal_id) === i
      )
      setResults(uniqueResults)
    } catch {
      setResults([])
    } finally {
      setLoading(false)
    }
  }

  // Close dropdown when clicking outside (desktop)
  useEffect(() => {
    const listener = (e: MouseEvent) => {
      if (dropdownRef.current && !dropdownRef.current.contains(e.target as Node)) {
        setResults([])
      }
    }
    document.addEventListener('mousedown', listener)
    return () => document.removeEventListener('mousedown', listener)
  }, [])

  return (
    <>
      {/* NAVBAR */}
      <nav className="sticky top-0 z-50 bg-card/95 backdrop-blur border-b border-border">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center h-16">

            {/* Logo */}
            <Link href="/" className="flex items-center gap-2 group">
              <div className="w-8 h-8 bg-primary rounded-lg flex items-center justify-center neon-glow">
                <span className="text-white font-bold text-lg">A</span>
              </div>
              <span className="text-xl font-bold text-foreground hidden sm:inline group-hover:text-primary transition-colors">
                Stream4nzu
              </span>
            </Link>

            {/* Desktop Menu */}
            <div className="hidden md:flex items-center gap-8">
              <Link href="/" className="text-foreground hover:text-primary transition-colors text-sm font-medium">Home</Link>
              <Link href="/popular" className="text-foreground hover:text-primary transition-colors text-sm font-medium">Popular</Link>
              <Link href="/trending" className="text-foreground hover:text-primary transition-colors text-sm font-medium">Trending</Link>
              <Link href="/genres" className="text-foreground hover:text-primary transition-colors text-sm font-medium">Genres</Link>
            </div>

            {/* Desktop Search */}
            <div className="relative hidden sm:flex items-center gap-2 bg-input border border-border rounded-lg px-3 py-2 w-64">
              <Search size={18} className="text-muted-foreground" />
              <input
                type="text"
                placeholder="Search anime..."
                value={searchQuery}
                onChange={(e) => {
                  setSearchQuery(e.target.value)
                  handleSearch(e.target.value)
                }}
                className="bg-transparent outline-none text-sm flex-1 placeholder:text-muted-foreground"
              />

              {/* Dropdown Desktop */}
              {results.length > 0 && (
                <div
                  ref={dropdownRef}
                  className="absolute top-full left-0 w-full mt-1 bg-card border border-border rounded-md shadow-lg max-h-80 overflow-y-auto z-50"
                >
                  {results.slice(0, 10).map((anime) => (
                    <Link
                      key={anime.mal_id}
                      href={`/anime/${anime.mal_id}`}
                      onClick={() => setResults([])}
                      className="flex items-center gap-3 px-3 py-2 w-full text-left hover:bg-primary/10 transition-colors"
                    >
                      <img
                        src={anime.images.jpg.small_image_url}
                        alt={anime.title}
                        className="w-10 h-14 object-cover rounded"
                      />
                      <div>
                        <p className="text-sm font-medium">{anime.title}</p>
                        <p className="text-xs text-muted-foreground">{anime.episodes ?? 'TBA'} eps</p>
                      </div>
                    </Link>
                  ))}
                </div>
              )}
            </div>

            {/* Right (Theme + Mobile) */}
            <div className="flex items-center gap-2">
              {/* Mobile Search */}
              <button
                className="sm:hidden p-2 hover:bg-secondary rounded-lg transition-colors"
                onClick={() => setMobileSearchOpen(true)}
              >
                <Search size={24} />
              </button>

              <ThemeToggle />

              {/* Mobile Menu */}
              <button
                className="md:hidden p-2 hover:bg-secondary rounded-lg transition-colors"
                onClick={() => setIsOpen(!isOpen)}
              >
                {isOpen ? <X size={24} /> : <Menu size={24} />}
              </button>
            </div>
          </div>

          {/* MOBILE MENU */}
          {isOpen && (
            <div className="md:hidden mt-2 space-y-2 px-2 pb-2">
              <Link href="/" className="block px-3 py-2 hover:text-primary">Home</Link>
              <Link href="/popular" className="block px-3 py-2 hover:text-primary">Popular</Link>
              <Link href="/trending" className="block px-3 py-2 hover:text-primary">Trending</Link>
              <Link href="/genres" className="block px-3 py-2 hover:text-primary">Genres</Link>
            </div>
          )}
        </div>
      </nav>

      {/* FULLSCREEN MOBILE SEARCH */}
      {mobileSearchOpen && (
        <div className="fixed inset-0 bg-background/95 backdrop-blur-lg z-[999] p-4 pointer-events-auto animate-fadeIn">
          <div className="flex items-center justify-between mb-4">
            <h2 className="text-lg font-semibold">Search Anime</h2>
            <button
              onClick={() => { setMobileSearchOpen(false); setResults([]) }}
              className="p-2 rounded-lg hover:bg-secondary"
            >
              <X size={26} />
            </button>
          </div>

          <div className="flex items-center gap-3 bg-input border border-border rounded-xl px-4 py-3">
            <Search size={20} className="text-muted-foreground" />
            <input
              autoFocus
              type="text"
              placeholder="Search anime..."
              value={searchQuery}
              onChange={(e) => {
                setSearchQuery(e.target.value)
                handleSearch(e.target.value)
              }}
              className="bg-transparent outline-none text-base flex-1"
            />
          </div>

          <div className="mt-4 space-y-2 max-h-[70vh] overflow-y-auto">
            {loading && <p className="text-sm text-muted-foreground">Loading...</p>}

            {results.length > 0 &&
              results.map((anime) => (
                <button
                  key={anime.mal_id}
                  onClick={() => {
                    setMobileSearchOpen(false) // tutup modal dulu
                    router.push(`/anime/${anime.mal_id}`) // baru navigasi
                  }}
                  className="flex items-center gap-3 p-2 rounded-lg hover:bg-primary/10 w-full text-left transition"
                >
                  <img
                    src={anime.images.jpg.small_image_url}
                    alt={anime.title}
                    className="w-12 h-16 object-cover rounded"
                  />
                  <div>
                    <p className="font-medium">{anime.title}</p>
                    <p className="text-xs text-muted-foreground">{anime.episodes ?? 'TBA'} eps</p>
                  </div>
                </button>
              ))}
          </div>
        </div>
      )}
    </>
  )
}
